@import "variables.scss";

@font-face {
  font-family: 'GothamPro';
  src: url('../fonts/GothamPro.eot');
  src: url('../fonts/GothamPro.eot?#iefix') format('embedded-opentype'),
  url('../fonts/GothamPro.woff') format('woff'),
  url('../fonts/GothamPro.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'GothamPro';
  src: url('../fonts/GothamPro-Light.eot');
  src: url('../fonts/GothamPro-Light.eot?#iefix') format('embedded-opentype'),
  url('../fonts/GothamPro-Light.woff') format('woff'),
  url('../fonts/GothamPro-Light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'GothamPro';
  src: url('../fonts/GothamPro-Medium.eot');
  src: url('../fonts/GothamPro-Medium.eot?#iefix') format('embedded-opentype'),
  url('../fonts/GothamPro-Medium.woff') format('woff'),
  url('../fonts/GothamPro-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'GothamPro';
  src: url('../fonts/GothamPro-Bold.eot');
  src: url('../fonts/GothamPro-Bold.eot?#iefix') format('embedded-opentype'),
  url('../fonts/GothamPro-Bold.woff') format('woff'),
  url('../fonts/GothamPro-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}


* {
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-width: 320px;

  font-family: $font-family;
  transition: 0.3s;

  background: $gray;



  &.overfl {
    overflow: hidden;
    height: 100vh;
  }

  &.dark-theme {
      background: $black-bg;
  }
}

.wrapper {
  margin-bottom: 32px;
  position: relative;
  z-index: 2;

  @media screen and(max-width: 991px){
    margin-bottom: 20px;
  }
}

.start-page {
  padding: 30px;

  &-logo {
    display: block;
    margin-bottom: 30px;
  }

  ul {
    display: inline-block;
    vertical-align: top;
    padding-right: 60px;

    li {
      padding: 5px 0;
       color: $gr;

      b {
        font-size: 20px;
      }

      a {
        font-size: 16px;
        line-height: 140%;
        color: $black;

        .dark-theme & {
          color: $white;
        }

        &:hover {
          //color: $active;
        }

        .red {
          color: red;
        }
      }
    }
  }
}

div,
span,
a,
input,
textarea,
select,
button,
.button {
  &:focus {
    outline: none;
  }
}

input,
textarea {
  box-shadow: none!important;
}


ul {
  margin: 0;
  padding: 0;

  li {
    list-style: none;
  }
}

a {
  cursor: pointer;
  transition: 0.3s;
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}

svg {
  use {
    transition: 0.3s;
  }
}

.w-100 {
  width: 100%!important;
}


.button {
  background: transparent;
  border: none;
  box-shadow: none;
  outline: none;
  cursor: pointer;

  @media screen and(max-width: 991px){
    .n {
      display: none;
    }
  }

  &-primary {
    display: inline-flex;
    align-items: flex-start;

    background: $white;
    border-radius: 3px;
    box-shadow: 0px 4px 12px -10px rgba(0, 102, 255, 0.07);


    .icon {
      width: 35px;
      height: 35px;
      display: inline-flex;
      align-items: center;
      justify-content: center;

      background: linear-gradient(315deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.38) 100%), #FFAD08;
      border-radius: 3px 0px 0px 3px;

      svg {
        width: 19px;
        height: 19px;

        use {
          color: $white;
        }
      }
    }

    .tx {
      width: calc(100% - 73px);
      height: 35px;
      padding: 0 20px;
      display: inline-flex;
      align-items: center;
      justify-content: center;

      position: relative;

      font-weight: 700;
      font-size: 11px;
      line-height: 11px;
      color: $black2;
      letter-spacing: 0.001em;
      text-transform: uppercase;
      transition: 0.3s;

      .nn {
        @media screen and (max-width: 991px) {
          display: none;
        }
      }


      span {
        position: relative;
        z-index: 2;
      }


      &:before {
        content: '';
        width: 100%;
        height: 100%;
        display: block;

        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;

        background-image: url("../images/svg/pattern.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        transition: 0.3s;
      }
    }

    &.w {
      .tx {
        width: 100%;
      }
    }

    &.b {
      border: 0.5px solid rgba(255, 255, 255, 0.3);

      background: $gray4;

      .tx {
        width: 100%;
        color: #333333;

        &:before {
          opacity: 1;
        }
      }
    }

    &:hover {
      background: $black3;

      .tx {
        color: $white;

        &:before {
          opacity: 0.1;
        }
      }
    }

    &:active {
      background: $black4;

      .tx {
        color: $white;

        &:before {
          opacity: 0.1;
        }
      }
    }
  }

  &-secondary {
    display: inline-flex;
    align-items: flex-start;

    background: $white;
    border-radius: 3px;
    box-shadow: 0px 4px 12px -10px rgba(0, 102, 255, 0.07);


    .icon {
      width: 35px;
      height: 35px;
      display: inline-flex;
      align-items: center;
      justify-content: center;

      background: linear-gradient(135deg, #076DE3 0%, #AD4CD1 100%);
      border-radius: 3px 0px 0px 3px;

      svg {
        width: 19px;
        height: 19px;

        use {
          color: $white;
        }
      }
    }



    .tx {
      width: calc(100% - 73px);
      height: 35px;
      padding: 0 20px;
      display: inline-flex;
      align-items: center;
      justify-content: center;

      position: relative;

      font-weight: 700;
      font-size: 11px;
      line-height: 11px;
      color: $black2;
      letter-spacing: 0.001em;
      text-transform: uppercase;
      transition: 0.3s;


      span {
        position: relative;
        z-index: 2;
      }


      &:before {
        content: '';
        width: 100%;
        height: 100%;
        display: block;

        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;

        background-image: url("../images/svg/pattern.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        transition: 0.3s;
      }
    }

    &.w {
      .tx {
        width: 100%;
      }
    }

    &:hover {
      background: $black3;

      .tx {
        color: $white;

        &:before {
          opacity: 0.1;
        }
      }
    }

    &:focus {
      background: $black4;

      .tx {
        color: $white;

        &:before {
          opacity: 0.1;
        }
      }
    }
  }

  &-lt {
    height: 60px;
    padding: 0 24px 0 14px;
    display: inline-flex;
    align-items: center;

    position: relative;

    border: 1px solid rgba(51, 51, 51, 0.1);
    border-radius: 5px;

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-sizing: border-box;

    &-min {
      height: 48px;

      .button-lt {
        &-icon {
          width: 24px;
          height: 24px;

          img {
            width: 18px;
          }
        }

        &-tx {
          font-size: 14px;
        }
      }
    }

    &-min2 {
      height: 32px;
      padding: 12px;

      .button-lt {
        &-icon {
          width: 24px;
          height: 24px;

          img {
            width: 18px;
          }
        }

        &-tx {
          font-size: 10px;
          line-height: 10px;
        }
      }
    }

    &:before {
      content: '';
      width: 100%;
      height: 100%;
      display: block;

      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;

      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% 100%;
      transition: 0.3s;
      opacity: 0;
      background-image: url("../images/header-icon-bg-hover1.jpg");
    }


    &:hover {
      border-color: transparent;

      &:before {
        opacity: 1;
      }

      .button-lt {
        &-icon {
          opacity: 0;
        }

        &-tx {
          color: $white;
        }
      }
    }

    &-icon {
      width: 36px;
      height: 36px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      margin-right: 13px;

      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      transition: 0.3s;
      background-image: url("../images/header-icon-bg-min1.jpg");

      border-radius: 3px;
      overflow: hidden;
    }

    &-tx {
      display: inline-block;

      position: relative;
      z-index: 4;

      font-size: 16px;
      line-height: 15px;
      color: $black2;
    }
  }

  &-ltr {
    height: 60px;
    padding: 0 24px 0 14px;
    display: inline-flex;
    align-items: center;

    position: relative;

    border: 1px solid rgba(51, 51, 51, 0.1);
    border-radius: 5px;

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-sizing: border-box;

    &:before {
      content: '';
      width: 100%;
      height: 100%;
      display: block;

      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;

      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% 100%;
      transition: 0.3s;
      opacity: 0;
      background-image: url("../images/header-icon-bg-hover2.jpg");
    }


    &:hover {
      border-color: transparent;

      &:before {
        opacity: 1;
      }

      .button-ltr {
        &-icon {
          opacity: 0;
        }

        &-tx {
          color: $white;
        }
      }
    }

    &-icon {
      width: 36px;
      height: 36px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      margin-right: 13px;

      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      transition: 0.3s;
      background-image: url("../images/header-icon-bg-min2.jpg");

      border-radius: 3px;
      overflow: hidden;
    }

    &-tx {
      display: inline-block;

      position: relative;
      z-index: 4;

      font-size: 16px;
      line-height: 15px;
      color: $black2;
    }

    &-min {
      height: 48px;

      .button-ltr {
        &-icon {
          width: 24px;
          height: 24px;

          img {
            width: 18px;
          }
        }

        &-tx {
          font-size: 14px;
        }
      }
    }

    &-min2 {
      height: 32px;
      padding: 0 12px;

      .button-ltr {
        &-icon {
          width: 24px;
          height: 24px;

          img {
            width: 18px;
          }
        }

        &-tx {
          font-size: 10px;
          line-height: 10px;
        }
      }
    }
  }


}

.container {
  max-width: 1480px;
  padding: 0 20px;
  margin: auto;

  box-sizing: border-box;
}


h1 {
  margin-bottom: 36px;

  position: relative;
  z-index: 4;

  font-weight: 700;
  font-size: 40px;
  line-height: 130%;
  color: $black2;
  text-transform: uppercase;

  @media screen and(max-width: 1380px){
    font-size: 35px;
  }

  @media screen and(max-width: 991px){
    margin-bottom: 16px;

    font-size: 20px;
    line-height: 130%;
  }
}
h2 {
  margin-bottom: 36px;

  position: relative;
  z-index: 4;

  font-weight: 700;
  font-size: 40px;
  line-height: 130%;
  color: $black2;
  text-transform: uppercase;

  @media screen and(max-width: 991px){
    margin-bottom: 20px;

    font-size: 24px;
    line-height: 130%;
  }
}




.header {
  padding: 17px 0;

  background: $white;
  box-shadow: 0px 15px 35px -5px rgba(0, 10, 255, 0.07);

  @media screen and(max-width: 991px){
    background: transparent;
    box-shadow: none;
    padding: 0;
  }

  &__user {
    display: inline-flex;
    align-items: center;

    @media screen and(max-width: 991px){
      width: 100%;
      padding:  0 20px;
      position: absolute;
      top: 0;
      left: 0;
    }

    &-ava {
      width: 59px;
      height: 59px;

      position: relative;
      z-index: 2;
      cursor: pointer;
      border-radius: 100px;
      overflow: hidden;

      img {
        max-width: 100%;
      }

      @media screen and(max-width: 991px){
        width: 75px;
        height: 75px;
      }
    }

    &-bl {
      width: 160px;
      margin-left: -10px;

      @media screen and(max-width: 991px){
        margin-left: 0;
        width: calc(100% - 153px);
        padding-left: 12px;
        margin-left: 0;
        box-sizing: border-box;
      }

      &-name {
        margin-bottom: 10px;
        font-weight: 700;
        font-size: 20px;
        line-height: 19px;
        color: $black2;

        @media screen and(min-width: 991px){
          display: none;
        }
      }

      &-bt {


        @media screen and(max-width: 991px){
          width: 35px;
          height: 35px;

          display: inline-flex;
          align-items: center;
          justify-content: center;

          position: absolute;
          right: 0;
          top: 0;
          z-index: 2;

          background: linear-gradient(315deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.38) 100%), #FFAD08;
          border-radius: 0px 3px 3px 0px;
        }

        @media screen and(min-width: 991px){
          width: 100%;
          height: 100%;

          position: absolute;
          top: 0;
          left: 0;
          z-index: 2;

          img {
            display: none;
          }
        }
      }

      &-in {
        position: relative;


        border-radius: 3px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        box-sizing: border-box;

        @media screen and(max-width: 991px){

        }
      }

      &-inner {
        height: 35px;
        padding: 12px 24px;

        box-sizing: border-box;
        background: $gray;
        border-radius: 5px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 11px;
        line-height: 11px;
        color: rgba(51,51,51,0.5);

        @media screen and(max-width: 991px){
          padding: 10px 40px 10px 12px;
          border: 1px solid rgba(51, 51, 51, 0.26);
        }

      }
    }
  }

  &__mob {
    display: none;

    position: relative;
    z-index: 999;

    @media screen and(max-width: 991px){
      padding: 17px 20px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      box-shadow: 0px 15px 35px -5px rgba(0, 10, 255, 0.07);
      background: $white;
      margin: 0 -20px;

    }

    &-menu {
      display: none;

      @media screen and(max-width: 991px){
        display: block;

        padding: 92px  13px 150px;

      }

      ul {
        li {
          margin-bottom: 36px;

          a {
            font-weight: 500;
            font-size: 24px;
            line-height: 23px;
            color: $black2;
          }
        }
      }
    }
  }

  &__tgl {
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;

    .op {
      display: block;
    }

    .cl {
      display: none;
    }

    svg {
      width: 24px;
      height: 24px;

      use {
        color: $black2;
      }
    }

    .opn & {
      .op {
        display: none;
      }

      .cl {
        display: block;
      }
    }
  }

  &__left,
  &__right,
  &__inner {
    display: flex;
    align-items: center;
  }

  &__right {

    @media screen and(max-width: 991px){
      height: 120px;
      align-items: flex-end;

      background: $gray3;
      padding: 0 20px 25px;
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;

      .button {
        position: absolute;
        top: 25px;
        left: 20px;
      }
    }
  }

  &__inner {
    justify-content: space-between;

    @media screen and(max-width: 991px){
      display: none;
      padding-bottom: 120px;

      .opn & {
        display: block;
        width: 100%;
        height: 100vh;
        min-height: 100%;

        position: fixed;
        top: 0;
        left: 0;
        z-index: 99;

        overflow: auto;
        background: $gray3;
      }

      .header__logo {
        display: none;
      }
    }
  }

  &__logo {
    margin-right: 70px;

    @media screen and(max-width: 1200px){
      margin-right: 20px;

      img {
        width: 170px;
      }
    }

     .black {
       display: block;
     }

     .white {
       display: none;
     }
  }

  &__links {
    @media screen and(max-width: 991px){
      display: none;
    }

    &-item {
      height: 60px;
      padding: 0 24px 0 14px;
      margin-right: 24px;
      display: inline-flex;
      align-items: center;

      position: relative;

      border: 1px solid rgba(51, 51, 51, 0.1);
      border-radius: 5px;

      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      box-sizing: border-box;
      overflow: hidden;

      @media screen and(max-width: 1200px){
        margin-right: 10px;
        padding: 0 14px 0 14px;

        font-size: 14px;

        &:last-of-type {
          margin-right: 0;
        }
      }

      &.bg1,
      &.bg2 {
        &:before {
          content: '';
          width: 100%;
          height: 100%;
          display: block;

          position: absolute;
          top: 0;
          left: 0;
          z-index: 1;

          background-repeat: no-repeat;
          background-position: center;
          background-size: 100% 100%;
          transition: 0.3s;
          opacity: 0;
        }
      }

      &.bg1 {
        &:before {
          background-image: url("../images/header-icon-bg-hover1.jpg");
        }

      }

      &.bg2 {
        &:before {
          background-image: url("../images/header-icon-bg-hover2.jpg");
        }

      }


      &:hover,
      &.active {
        border-color: transparent;

        &.bg1,
        &.bg2 {
          &:before {
            opacity: 1;
          }
        }

        .header__links {
          &-icon {
            opacity: 1;
            position: relative;
            z-index: 3;
            background-image: none;

            img {

            }
          }

          &-tx {
            color: $white;

          }
        }

      }
    }

    &-icon {
      width: 36px;
      height: 36px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      margin-right: 13px;

      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      transition: 0.3s;

      border-radius: 3px;
      overflow: hidden;


      .bg1 & {
        background-image: url("../images/header-icon-bg-min1.jpg");
      }
      .bg2 & {
        background-image: url("../images/header-icon-bg-min2.jpg");
      }
    }

    &-tx {
      display: inline-block;

      position: relative;
      z-index: 4;

      font-weight: 500;
      font-size: 16px;
      line-height: 15px;
      color: $black2;
    }
  }

  &__how {
    margin-right: 16px;
    height: 35px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 7px 18px;

    border: 1px solid rgba(51, 51, 51, 0.1);
    border-radius: 5px;
    box-sizing: border-box;

    svg {
      width: 21px;
      height: 21px;
      margin-right: 4px;

      use {
        color: rgba(51, 51, 51, 0.4);
      }
    }

    span {
      color: rgba(51, 51, 51, 0.4);
      font-weight: 700;
      font-size: 11px;
      line-height: 11px;
    }
  }

  &__settings {
    margin-right: 16px;

    &-title {
      height: 35px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      padding: 0 6px;

      box-sizing: border-box;

      border: 1px solid rgba(51, 51, 51, 0.1);
      border-radius: 5px;

      svg {
        width: 21px;
        height: 21px;

        use {
          color: rgba(51, 51, 51, 0.4);
        }
      }
    }
  }

  &__lang {
    width: 75px;
    margin-right: 48px;

    position: relative;

    @media screen and(max-width: 1200px){
      margin-right: 10px;
    }

    &-title {
      display: flex;
      align-items: center;
      height: 35px;
      padding: 0 10px;

      border: 1px solid rgba(51, 51, 51, 0.1);
      border-radius: 5px;
      cursor: pointer;
    }

    &-fl {
      width: 16px;

      img {
        max-width: 100%;
      }
    }

    &-tit {
      margin: 0 4px 0 10px;

      text-transform: uppercase;
      font-weight: 700;
      font-size: 11px;
      line-height: 11px;
      color: $black2;
    }

    &-ar {
      width: 8px;
      height: 5px;
      display: inline-flex;
      transition: 0.3s;

      svg {
        width: 8px;
        height: 5px;

        use {
          color: $black2;
        }
      }

      .open & {
        transform: rotate(-180deg);
      }
    }

    &-drop {
      width: 100%;
      padding: 13px 15px;
