.swiper {
  &-slide-shadow-left,
  &-slide-shadow-right {
    background-image: none!important;
    opacity: 0!important;
  }
  &-button {
    &-next,
    &-prev {
      display: inline-block;
      width: 10px;
      height: 15px;
      position: relative;
      top: 0;
      left: inherit;
      right: inherit;

